import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, Navigate, useParams } from "react-router-dom";
import { deleteCartProductAsync } from "../features/cart/cartSlice";
import { resetCurrentOrder } from "../features/orders/orderSlice";

export default function OrderSuccess() {
  const currentOrder = useSelector((state) => state.order.currentOrder);
  const cartItems = useSelector((state) => state.cart.items);
  const cartItemsLoad = useSelector((state) => state.cart.cartLoaded);
  const dispatch = useDispatch();
  const parems = useParams();
  useEffect(() => {
    removeCartProducts();
  }, []);
  useEffect(() => {
    removeCartProducts();
  }, [cartItemsLoad]);
  function removeCartProducts() {
    if (cartItems) {
      cartItems.forEach((element) => {
        dispatch(deleteCartProductAsync(element.id));
        dispatch(resetCurrentOrder());
      });
    }
  }
  return (
    <>
      {!parems?.id && <Navigate to="/" replace={true}></Navigate>}

      <main className="grid min-h-full place-items-center bg-white px-6 py-24 sm:py-32 lg:px-8">
        <div className="text-center">
          <p className="text-3xl font-semibold text-indigo-600">
            Congratulations!
          </p>
          <h1 className="mt-4 text-3xl font-bold tracking-tight text-gray-900 sm:text-5xl">
            Your Order is placed
          </h1>
          <h1 className="mt-4 text-3xl font-bold tracking-tight text-gray-900 sm:text-5xl">
            Order Number: #{parems?.id}
          </h1>
          <p className="mt-6 text-base leading-7 text-gray-600"></p>
          <div className="mt-10 flex items-center justify-center gap-x-6">
            <Link
              to="/"
              className="rounded-md bg-indigo-600 px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
            >
              Go back home
            </Link>
            <Link
              to="/usersOrders"
              className="rounded-md bg-indigo-600 px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
            >
              You can check your order here
            </Link>
            <a href="#" className="text-sm font-semibold text-gray-900">
              Contact support <span aria-hidden="true">&rarr;</span>
            </a>
          </div>
        </div>
      </main>
    </>
  );
}
